<template>
  <div class="tuoguan">
    <div v-if="$route.name === '公号托管'">
      <Button class="create" type="success" to="/manage/gonghao/tuoguan/create">接管新公号</Button>
      <Input
        style="width: 200px;margin-right: 30px;"
        size="large"
        type="text"
        v-model="name"
        placeholder="请输入公号名"
      ></Input>
      <Button size="large" type="primary" @click="search">搜索</Button>
      <Button style="float:right;" type="warning" @click="reset">
        <span style="color: #000;">(慎用)</span>
        重置code
      </Button>
      <div style="clear: both;"></div>
      <RadioGroup class="gonghao-tag" v-model="gonghaoTag" type="button" @on-change="showDataList">
        <Radio label="全部"></Radio>
        <Radio v-for="(item, index) in tagList" :label="item.name"></Radio>
      </RadioGroup>
      <Table class="tuoguan-table" stripe :columns="tuoguanHeader" :data="tuoguanData"></Table>
      <Page show-total :total="dataList.length" :page-size="10" @on-change="changePage"/>
    </div>
    <router-view/>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isCreate: true,
      code: null,
      name: "",
      gonghaoTag: "全部",
      tagList: [],
      dataList: [],
      tuoguanData: [],
      tuoguanHeader: [
        {
          key: "code",
          title: "code",
          align: "center",
          width: 80
        },
        {
          key: "name",
          title: "公号名称",
          align: "center"
        },
        {
          title: "分组",
          align: "center",
          render: (h, params) => {
            let group = params.row.group == "" ? "暂未分组" : params.row.group;
            return h(
              "span",
              {
                props: {}
              },
              group
            );
          }
        },
        {
          key: "token",
          title: "token",
          align: "center"
        },
        {
          title: "EncodingAESKey",
          align: "center",
          width: 140,
          render: (h, params) => {
            let EncodingAESKey = params.row.EncodingAESKey;
            return h(
              "div",
              {
                style: {
                  padding: "5px 0"
                }
              },
              EncodingAESKey
            );
          }
        },
        {
          key: "url",
          title: "链接",
          align: "center",
          render: (h, params) => {
            let url = "http://yysd.dmmup.com/weichat/" + params.row.code;
            return h(
              "span",
              {
                props: {}
              },
              url
            );
          }
        },
        {
          title: "公号属性",
          align: "center",
          render: (h, params) => {
            let attribute =
              params.row.attribute == 0 ? "默认" : (params.row.attribute == 1 ? '男' : '女');
            return h(
              "span",
              {
                props: {}
              },
              attribute
            );
          }
        },
        {
          title: "接管状态",
          align: "center",
          render: (h, params) => {
            let status =
              params.row.status === -2
                ? "未接管"
                : params.row.status === -1
                ? "接管中"
                : "已接管";
            return h(
              "span",
              {
                props: {}
              },
              status
            );
          }
        },
        {
          title: "是否实时更新用户信息",
          align: "center",
          render: (h, params) => {
            let real_time =
                params.row.real_time
                    ? "是"
                    : "否";
            return h(
                "span",
                {
                  props: {}
                },
                real_time
            );
          }
        },
        {
          title: "Action",
          align: "center",
          width: 280,
          render: (h, params) => {
            let that = this;
            return h("div", [
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.$router.push({
                        name: "修改公号",
                        query: params.row
                      });
                    }
                  }
                },
                "编辑"
              ),
              h(
                "Button",
                {
                  style: {
                    display: params.row.status !== -2 ? "inline-block" : "none",
                    marginRight: "10px"
                  },
                  props: {
                    type: "warning",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.$router.push({
                        name: "配置公号",
                        query: { code: params.row.code }
                      });
                    }
                  }
                },
                "配置"
              ),
              h(
                "Button",
                {
                  props: {
                    type: "error",
                    size: "small"
                  },
                  style: {
                    marginRight: "10px"
                  },
                  on: {
                    click() {
                      that.$Modal.confirm({
                        title: "提示:",
                        content: "确认删除这条内容吗？",
                        onOk: () => {
                          that.$axios
                            .get("/conf/del", {
                              params: { id: params.row._id }
                            })
                            .then(res => {
                              that.tuoguanData.splice(params.index, 1);
                              that.$Message.info(res.data.success);
                            });
                        }
                      });
                    }
                  }
                },
                "删除"
              ),
              h(
                "Button",
                {
                  style: {
                    display: params.row.status == -2 ? "inline-block" : "none"
                  },
                  props: {
                    type: "success",
                    size: "small",
                    disabled: params.row.status == -2 ? false : true
                  },
                  on: {
                    click() {
                      that.$Modal.confirm({
                        title: "提示:",
                        content: "确认开始接管吗？",
                        onOk: () => {
                          that.$axios
                            .get("/conf/jieguan", {
                              params: { code: params.row.code }
                            })
                            .then(res => {
                              that.$Message.info("开始接管");
                              that.$router.go(0);
                            });
                        }
                      });
                    }
                  }
                },
                "接管"
              )
            ]);
          }
        }
      ]
    };
  },
  mounted() {
    this.showDataList();
    this.showTagList();
  },
  methods: {
    search() {
      let name = this.name.trim();
      if (name != "") {
        this.$axios
          .get("/conf/find_one", { params: { name: this.name } })
          .then(res => {
            if (res.data.data.length != 0) {
              this.tuoguanData = res.data.data;
            } else {
              this.showDataList();
              this.$Message.info(
                "没有查询到包含该字符的公号，请检查后重新输入"
              );
            }
            this.name = "";
          });
      } else {
        this.$Message.info("公号名不能为空");
      }
    },
    showTagList() {
      this.$axios.get("/gonghaoTag").then(res => {
        this.tagList = res.data.data;
      });
    },
    showDataList() {
      this.$axios.get("/conf").then(res => {
        let data = res.data.data;
        if (this.gonghaoTag == "全部") {
          this.dataList = data;
          this.tuoguanData = this.dataList.slice(0, 10);
        } else {
          this.dataList = [];
          for (let i = 0; i < data.length; i++) {
            if (this.gonghaoTag === data[i].group) {
              this.dataList.push(data[i]);
            }
          }
          this.tuoguanData = this.dataList.slice(0, 10);
        }
      });
    },
    reset() {
      this.$axios.get("/conf/reset").then(res => {
        this.$Message.info(res.data.success);
      });
    },
    changePage(index) {
      var _start = (index - 1) * 10;
      var _end = index * 10;
      this.tuoguanData = this.dataList.slice(_start, _end);
      this.goTop();
    },
    goTop() {
      let self = this;
      self.timer = setInterval(function() {
        let osTop =
          document.documentElement.scrollTop || document.body.scrollTop;
        let ispeed = Math.floor(-osTop);
        document.documentElement.scrollTop = document.body.scrollTop =
          osTop + ispeed;
        self.isTop = true;
        if (osTop === 0) {
          clearInterval(self.timer);
        }
      }, 30);
    }
  }
};
</script>

<style scoped>
.create,
.gonghao-tag {
  margin-bottom: 30px;
  float: left;
}

.tuoguan-table {
  width: 100%;
  clear: both;
  margin-bottom: 30px;
}
</style>
